import React, { Component } from 'react';
import './App.css';
import OutputFlames from './OutputFlames/OutputFlames';


class App extends Component {

  constructor(props){
    super(props);
    this.onchangeSubmit = this.onchangeSubmit.bind(this);
  }
  state = {
    Output : ""
  }
  
  changeUpperText1 = (event) => {
      const typedText = (event.target.value).slice(-1);
      const letterNumber = /^[0-9]+$/;
      if((typedText.match(letterNumber))) {
        alert("Please Enter Only Charecters");
      } 
  }

  changeUpperText2 = (event) => {
    const typedText = (event.target.value).slice(-1);
    const letterNumber = /^[0-9]+$/;
    if((typedText.match(letterNumber))) {
      alert("Please Enter Only Charecters");
    } 
}

onchangeSubmit = () => {
  const text1 = document.getElementById("YourName").value;
  const text2 = document.getElementById("PartnerName").value;
  if ( text1 === "" || text2 ===""){
   alert("Please Fill the Inputs")
  }
  else{
    let  i, j,ch, count=0, grantTotal=0;
    const string1 = text1.split('');
    const string2 = text2.split('');
    let n = string1.length;
    let m = string2.length;
    let total = n + m;
    for (i = 0; i < n; i++)  
    { 
        ch = string1[i]; 
        for (j = 0; j < m; j++) 
        { 
            if (ch === string2[j]) 
            { 
                string1[i] = -1; 
                string2[j] = -1; 
                count = count + 2; 
                break; 
            } 
        } 
    } 
    grantTotal = total - count;
    let ans;
    if (grantTotal === 0){
       alert("No Match Found")
    }
    else if (grantTotal % 6 === 0){
      ans = 6;
    }
    else{
      ans = grantTotal % 6;
    }

    this.setState({Output : ans -1});

  }
}

  render(){
  return (
    <div className="App">
      <div className = "Container">
      <div className = "YourName" >
            <h1>Enter Your Name</h1>
            <input id = "YourName" onChange = {this.changeUpperText1} required></input>
        </div>
        <div className = "PartnerName">
            <h1>Enter Your Partner Name</h1>
            <input id = "PartnerName" onChange = {this.changeUpperText2} required></input>
        </div>
        <button onClick={this.onchangeSubmit.bind(this)}>Calculate</button>
        <OutputFlames output = {this.state.Output} />
      </div>
    </div>
  );
}
}

export default App;
