const OutputFlames = (props) => {
    const style = {
        position : "absolute",
        left: "150px",
        top: "470px",
        fontSize: "70px",
        color: "yellow",
        

    }
    let flames=['FRIENDS','LOVERS','ATTRACTION','MARRIAGE','ENEMIES','SIBLINGS'];
    return(
         <div style = {style}>
             <p>{flames[props.output]}</p>
         </div>
    );
}

export default OutputFlames;